.clearfix:after,
.clearfix:before {
    content: " ";
    display: table;
    line-height: 0;
}
.clearfix:after {
    clear: both;
}
.clipped {
    border: 0;
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}
.clipped--stealth:focus {
    clip-path: none;
    height: auto;
    overflow: visible;
    white-space: normal;
    width: auto;
}
.image-stretch {
    height: auto;
    width: 100%;
}
.image-scale {
    height: auto;
    max-width: 100%;
}
.image-center {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}
.image-center img {
    max-height: 100%;
    max-width: 100%;
}
.image-treatment {
    align-items: center;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
}
.image-treatment:after {
    background: rgba(0, 0, 0, 0.05);
    content: "";
    display: block;
    inset: 0;
    pointer-events: none;
    position: absolute;
}
.image-treatment > img {
    display: inline-block;
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
}
.image-treatment-large {
    align-items: center;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    overflow: hidden;
    position: relative;
}
.image-treatment-large:after {
    background: rgba(0, 0, 0, 0.05);
    content: "";
    display: block;
    inset: 0;
    pointer-events: none;
    position: absolute;
}
.image-treatment-large > img {
    display: inline-block;
    max-height: 100%;
    max-width: 100%;
    object-fit: contain;
}
.image-disabled {
    filter: var(--color-media-disabled-filter);
}
.text-truncate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.scrollbars-permanent {
    scroll-behavior: smooth;
    scroll-snap-type: proximity;
    scroll-snap-type: x proximity;
}
.scrollbars-permanent::-webkit-scrollbar {
    background-color: var(--color-state-layer-focus);
    border-radius: 12px;
}
.scrollbars-permanent::-webkit-scrollbar:vertical {
    width: 6px;
}
.scrollbars-permanent::-webkit-scrollbar:horizontal {
    height: 6px;
}
.scrollbars-permanent::-webkit-scrollbar-thumb {
    background-color: var(--color-foreground-secondary);
    border-color: transparent;
    border-radius: 12px;
    border-right-style: inset;
    box-shadow: none;
}
